import React from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { Modal } from 'bootstrap';
import { useState, useEffect, useRef } from 'react';


const TeamButton = () =>
{
    const [modal, setModal] = useState(null)
    const exampleModal = useRef()

    useEffect(() => {
    setModal(
      new Modal(exampleModal.current)
    )
  }, [])
    return (
        <>
           <div className="team-button">
              <button type="button" className="btn btn-light" onClick={() => modal.show()}>Become a contributor</button>
           </div>
           <div className="modal fade" id="myModal" ref={exampleModal} tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
               <div className="modal-dialog">
                   <div className="modal-content">
                       <div className="modal-header">
                          <img src="/images/logo.png"/>
                       </div>
                       <form>
                         <div className="modal-body">
                             <h2>Swap into <span> KUUL </span>governence tokens:</h2>
                           
                              <div className="body-part">
                                 <img src = "/images/modal-image.png"/>
                                   <div className="mb-3 d-flex">
                                     <label className="form-label">Amount to be swapped</label>
                                     <input type="text" className="form-control" id="amount" name="amount" />
                                     <label className="form-label">WBNB</label>
                                    </div>
                              </div>
                              <div className="body-part-1 ">
                                    <img  className="arrow" src = "/images/arrow-1.png"/>
                                     <p className="label-1">Swap rate (fixed): </p>
                                     <p className=" label-2">1 WBNB = 35,000 KUUL</p>
                                    
                              </div>
                              <div className="body-part">
                                 <img src = "/images/intropage-logo.png"/>
                                   <div className="mb-3 d-flex">
                                     <label className="form-label">Amount to be received</label>
                                     <input type="text" className="form-control" id="amount" name="amount" />
                                     <label className="form-label">KUUL</label>
                                    </div>
                              </div>
                           
                            </div>
                            <div className="modal-footer justify-content-center">
                               <button type="button" className="btn btn-light ">Swap Now</button>
                            </div>
                        </form>
                    </div>
               </div>
           </div>
        </>
    );
}

export default TeamButton;