import React from 'react';
import Header from './Header';
import './LandingPage.css';
import Buttons from './Buttons';
import LeftSide from './LeftSide';
import RightSide from './RightSide';
import Bottom from './Bottom';
import Footer from './Footer';
import NewButtons from './NewButtons';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
const LandingPage = () =>
{
  return(
    <>  
     <Header/>
     <div className="container-fluid">
        <div className="row">
            <div className="col-sm-12">
               <h1 className="text-1">
                  Your Defi solution for <br></br>
                 <span className="green"> green </span> and sustainable  <br></br>
                  crypto
               </h1>
               <p className="para">
                  A community initiative to bring instant impact <br></br> and sustainability to the crypto industry
               </p>
               <p className="para-1">
                   Tweets from industy and opinion leaders are <br></br> helpful creating awareness, but action is better
               </p>
            </div>
        </div>
        <div className="row">
            <div className="col-sm-12">
                <h1 class="text-2">... so we’re launching a solution</h1>
            </div>
        </div>
        <div className="row">
            <div className="col-sm-12">
                <Buttons/>
            </div>
        </div>
        <div className="row">
            <div className="col-sm-12 inner-part">
               <LeftSide/>
               <div className="v1"></div>
               <RightSide/>
            </div>
        </div>
        <div className = "row">
           <div className="col-sm-12">
             <Bottom/>
           </div>
        </div>
        
           
             <NewButtons/>
           
        
     </div>
    <Footer/>
   </>
    
  );
}

export default  LandingPage;