import React from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { DropdownButton,Dropdown } from 'react-bootstrap';
import MenuIcon from '@material-ui/icons/Menu';
import '../LandingPages/Header.css';

const IntroHeader = () =>
{
    return (
        <>
            <div className="header">
                <div className="row">
                    <div className="col-sm-12 col-md-12">
                         <img src ="/images/logo.png" className="logo-img" alt="error"/>
                          <div className="header-right">
                                <div className="connect">
                                    <button type="button" class="btn btn-light">Connect Wallet</button>
                                </div>
                                <img src="/images/Discord.png" className="discord-logo"/>
                                <img src="/images/Telegram.png" className="Telegram-logo"/>
                                <DropdownButton id="dropdown-basic-button" title={<MenuIcon
                                    className="dropdown-icon"
                                />} className="dropdown" variant="transparent">
                                    <Dropdown.Item href="/">Landing Page</Dropdown.Item>
                                    <Dropdown.Item href="/intropage">Introduction Page</Dropdown.Item>
                                    <Dropdown.Item href="/crowdsale">CrowdSale Page</Dropdown.Item>
                                </DropdownButton>
                          </div>
                    </div>
                   
                </div>
            
           </div>
        </>
    );
  
}

export default IntroHeader;