import React from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';

const NewButtons = () =>
{
    return(
        <>
          <div className="newbuttons">
             <div className="row">
                <div className="col-sm-12">
                     <button type="button" class="btn btn-light btn-1">Get on our waitlist</button>
                </div>
                <div className="col-sm-12">
                     
                    <button type="button" class="btn btn-light btn-2">Become a contributor</button>
                </div>
             </div>
           </div>
        </>
    );
}

export default NewButtons;