import React from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';

const Buttons = () =>
{
    return (
        <>
           <div className="buttons">
              <button type="button" class="btn btn-light">Get on our waitlist</button>
              <button type="button" class="btn btn-light">Become a contributor</button>
           </div>
        </>
    );
}

export default Buttons;