import React from 'react';
import './App.css'
import LandingPage from './components/LandingPages/LandingPage';
import introPage from './components/introPages/introPage';
import Crowdsale from './components/Crowdsale/Crowdsale';
import { Route,Switch } from 'react-router-dom';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';

const App = () =>
{
    return(
      <>
         <div className="container-fluid">
            <Switch>
              <Route exact path = '/kuul' component={LandingPage}/>
              <Route  path='/intropage' component={introPage}/>
              <Route  path='/crowdsale' component={Crowdsale}/>
           </Switch>
         </div>
        
      </>
    );
}

export default App;